.editor-color {
  display: flex;
  position: relative;
  > .form-group {
    flex: 1 1 1%;
    padding-right: 5px;
    &:last-child {
      padding-right: 0;
    }
  }
}

.editor-color-remove {
  position: absolute;
  right: 0;
  top: -10px;
}



