@import (reference) "~ui/styles/mixins.less";

.metric-vis {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  align-content: space-around;

  .metric-value {
    font-weight: bold;
    .ellipsis();
  }

  .metric-container {
    text-align: center;
    padding: 1em;
  }
}



